.userName {
    font-weight: bold;
}

.InstaPost {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    padding: 10px;
    font-family: Arial, Helvetica, sans-serif;
    width: 600px;
    height: 100%;
    border-radius: 15px;
    gap: 10px;

    .header {
        display: flex;
        align-items: center;
        gap: 15px;

        .userIcon {
            border-radius: 50%;
            width: 80px;
            height: 80px;
            object-fit: cover;
        }

        .userInfo {
            .userSubtitle {}
        }
    }

    .main {
        display: flex;
        justify-content: center;

        .image {
            max-width: 100%;
            max-height: 100%;
        }
    }

    .footer {
        .actions {
            display: flex;
            flex-direction: row;
            align-items: center;
            gap: 10px;
            margin-bottom: 12px;

            .like {
                flex: 0 0 30px;
                height: 30px;
                fill: #fff;
                stroke: red;
                stroke-width: 15;
                transition: all 0.2s;
                cursor: pointer;

                &:hover {
                    fill: red;
                }
            }

            .comment {
                flex: 0 0 30px;
                height: 30px;
                stroke: black;
                stroke-width: 25;
                transition: all 0.2s;
                cursor: pointer;
                fill: #fff;

                circle {
                    fill: black;
                }

                &:hover {
                    fill: black;

                    circle {
                        fill: white;
                        stroke: #fff;
                    }
                }
            }

            .send {
                flex: 0 0 30px;
                height: 30px;
                margin-right: auto;
                stroke-width: 20;
                transition: all 0.2s;
                cursor: pointer;
                fill: #fff;
                stroke: black;

                &:hover {
                    fill: black;
                }
            }

            .save {
                flex: 0 0 30px;
                height: 30px;
                transition: all 0.2s;
                fill: #fff;
                cursor: pointer;
                stroke: black;

                &:hover {
                    fill: black;
                }
            }
        }

        .activity {
            .views {
                margin-bottom: 5px;
                font-weight: bold;
            }

            .userText {
                display: flex;

                .userName {
                    white-space: nowrap;
                    margin-right: 10px;
                }

                .text {}
            }
        }
    }
}