* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

::-webkit-scrollbar {
    width: 0px;
    background: rgba(255, 255, 255, 0.0);
}

body {
    height: 100%;
    background-color: #f7f5f5;
}

#root {}

.App {
    max-width: 1170px;
    padding: 15px;
    margin: 0 auto;
    height: 100%;
}

.home-page {
    .title {
        margin-bottom: 15px;
    }

    .text {}
}

.Post-list {
    display: flex;
    flex-direction: column;
    align-items: center;

    .post-list__title {
        gap: 10px;
        text-align: center;
        margin: 0px 0px 25px 0px;
    }
}

.InstaPosts {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 40px;
}

.Contacts {
    display: flex;
    flex-direction: column;
    align-items: center;
}