.Header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80px;
    width: 100%;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 18px;
    position: relative;
    margin-bottom: 20px;

    .logo {
        width: 70px;
        height: 70px;
    }

    .logo__image {
        width: 100%;
        height: 100%;
    }

    .links {
        display: flex;
        list-style-type: none;
        gap: 5px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .link {
        padding: 7px;
        text-decoration: none;
        color: inherit;
        transition: all .2s;

        &:hover {
            color: teal;
        }
    }
}